import TabHeader from "../tab/tab"
export default {
    components: {
        TabHeader
    },
    data() {
        return {

        }
    },
    created() {
        if (this.$route.query.uid) {
            // 清除
            localStorage.removeItem('loginopenid')
            localStorage.setItem('loginopenid', this.$route.query.uid)
        }
    }
}




